.formChangeUser {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;

  img {
    width: 160px;
    height: 200px;
    max-width: 160px;
    max-height: 200px;
  }

  .label {
    display: flex;
    flex-direction: column;

    :not(:last-child) {
      margin-bottom: 10px;
    }
  }
}
